import api from "@/plugins/axios";
import characterTemplate from '@/store/modules/characterTemplate.js'

const state = () => ({
    notification: {
        message: '',
        type: '',
        state: false
    },
    armorPenality: 0,
    character: characterTemplate(),
    characterList: [],
    attributePoints: [1, 2, 3],
    maxAffinityLevel: {
        body: 1,
        mind: 1,
        ether: 1     
    },
    affinityPoints: {
        body: 0,
        mind: 0,
        ether: 0
    },
    maxHeritageAbilities: 2,
    maxSourceAbilities: 1,
    maxPathAbilities: 1,
    maxPathMagicAbilities: 1,
    maxInitialWeapons: 2,
    maxInitialArmor: 1,
    maxInitialShield: 1,
    skillDetail: [],
    spentSkillPoints: 0,
    specializations: [],
    size: "",
    move: 0,
    genderList: [],
    pathSelected: [],
    magicPathSelected: [],
    initialArmors: [],
    initialShields: [],
    initialWeapons: [],
    equipmentList: [],
    sorcery: false,
    maxSpells: 3,
    maxEnchants: 1,
    showCharacter: []
})

const getters = {}

const actions = {
    async showCharacter ({ commit }, id) {
        const characters = await api.get('/characters/?id=' +id)
        commit('setShowCharacter', characters.data.results)
    },
    async getCharacterList ({ commit }, userId) {
        const character = await api.get('/characters/?username=' +userId)
        commit('setCharacterList', character.data.results)
    },
    async deleteCharacter ({ commit }, id) {
        const url = '/characters/' + id + '/'
        await api.delete(url)
        commit('setDeleteCharacter')
    },
    async getGenders ({ commit }) {
        const genders = await api.get('/genders/')
        commit('setGenders', genders.data.results)
    },
    getCharacterHeritage ({ commit }, heritage ) {
        commit('setHeritage', heritage)
        commit('setHeritageAbility', heritage.path)
        commit('setSize', heritage.size)
        commit('setMove', heritage.move)
    },
    getCharacterSource ({ commit }, { source, primarySpecialization, secondarySpecialization } ) { 
        const sourceSkills = source
        commit('setSource', source)
        commit('setSourceAbility', source.path.abilities)
        commit('setSourceSkills', { sourceSkills, primarySpecialization, secondarySpecialization })
    },
    getCharacterMotivation({ commit }, motivation ) {
        commit('setMotivation', motivation)
    },
    updateAttributePoints({ commit }, updateAttribute) {
        commit('setUpdateAttributePoints', updateAttribute)
    },
    releaseAttributePoints({ commit }, updateAttribute) {
        commit('setReleaseAttributePoints', updateAttribute)
    },
    selectAffinityLevel({ commit }, affinityChoice ) {
        commit('setSelectAffinityLevel', affinityChoice )
    },
    saveSkill({ commit }, { skill, specializationSelected }) {
        commit('setSkills', { skill, specializationSelected })
    },
    spentSkillPoints({ commit }) {
        commit('setSpentSkillPoints')
    },
    addSkillPoints({ commit }) {
        commit('setAddSkillPoints')
    },
    getSkillDetail({ commit }, id) {
        commit('setSkillDetail', id)
    },
    clearSkillDetail({ commit }) {
        commit('setClearSkillDetail')
    },
    learnAbility({ commit }, ability) {
        commit('setAbility', ability)
    },
    async selectPath ({ commit }, id) {
        const path = await api.get('/paths/?id=' + id)

        if (path.data.results[0].source.name == 'Geral') {
            commit('setSelectPath', path.data.results)
        } else {
            commit('setSelectMagicPath', path.data.results)
        }
    },
    async getInitialArmors ({ commit }) {
        const initialArmors = await api.get('/armors/?initial=true')
        commit('setInitialArmors', initialArmors.data.results)
    },
    chooseArmor({ commit }, armor) {
        commit('setChooseArmor', armor)
    },
    async getInitialShields ({ commit }) {
        const shields = await api.get('/shields/')
        commit('setInitialShields', shields.data.results)
    },
    chooseShield({ commit }, shield) {
        commit('setChooseShield', shield)
    },
    async getInitialWeapons ({ commit }, specializations) {
        let weapons = []

        for (let specialization in specializations) {
            const weapon = await api.get('/weapons/?specialization=' +specializations[specialization])
            weapons.push(weapon.data.results)
        }
        
        commit('setInitialWeapons', weapons)
    },
    chooseWeapon({ commit }, weapon) {
        commit('setChooseWeapon', weapon)
    },
    clearSelectedPath ({ commit }) {
        commit('setClearSelectedPath')
    },
    clearSelectedMagicPath ({ commit }) {
        commit('setClearSelectedMagicPath')
    },
    getGeo ({ commit }) {
        commit('setGeo')
    },
    equipmentBuy ({ commit }, { equipment, equipmentCategory}) {
        commit('setEquipmentBuy', { equipment, equipmentCategory })
    },
    async increaseEquipment ({ commit }, equipment) {
        const getEquipment = await api.get('/' + equipment.type + '/?id=' + equipment.id)
        const equipmentResult = getEquipment.data.results
        commit('setIncreaseEquipment', { equipment, equipmentResult })
    },
    async sellEquipment ({ commit }, equipment) {
        const getEquipment = await api.get('/' + equipment.type + '/?id=' + equipment.id)
        const equipmentResult = getEquipment.data.results
        commit('setSellEquipment', { equipment, equipmentResult })
    },
    learnSpell({ commit }, spell) {
        commit('setLearnSpell', spell)
    },
    deleteSpell({ commit }, spell) {
        commit('setDeleteSpell', spell)
    },
    clearNotification({ commit }) {
        commit('setClearNotification')
    },
    clearNewCharacter({ commit }) {
        commit('setClearNewCharacter')
    }
}

const mutations = {
    setShowCharacter (state, character) {
        state.showCharacter = character[0]
    },
    setCharacterList (state, characters) {
        state.characterList = characters
    },
    async setDeleteCharacter (state) {
        const characters = await api.get('/characters/')
        state.characterList = characters.data.results
    },
    setGenders (state, genders) {
        state.genderList = genders
    },
    setHeritage (state, heritage) {
        state.character.heritage = heritage
    },
    setSize (state, size) {
        state.size = size
    },
    setMove (state, move) {
        state.move = move
    },
    setSource (state, source) {
        state.character.source = source
    },
    setMotivation (state, motivation) {
        state.character.motivation = motivation
    },
    setUpdateAttributePoints (state, updateAttribute) {
        state.attributePoints.splice(state.attributePoints.findIndex(index => index === updateAttribute.value), 1)
        state.character[updateAttribute.attribute] = updateAttribute.value
        state.maxAffinityLevel[updateAttribute.attribute] = updateAttribute.value
        state.affinityPoints[updateAttribute.attribute] = updateAttribute.value
    },
    setReleaseAttributePoints (state, updateAttribute) {
        state.attributePoints.push(updateAttribute.value)
        
        state.character[updateAttribute.attribute] = 1
        state.affinityPoints[updateAttribute.attribute] = 0

        if (updateAttribute.attribute === 'body') {
            state.character.agility = 0
            state.character.dexterity = 0
            state.character.strength = 0
            state.character.stamina = 0
        }

        if (updateAttribute.attribute === 'mind') {
            state.character.cunning = 0
            state.character.influence = 0
            state.character.perception = 0
            state.character.will = 0
        }

        if (updateAttribute.attribute === 'ether') {
            state.character.water = 0
            state.character.fire = 0
            state.character.air = 0
            state.character.earth = 0
            state.character.moon = 0
            state.character.nature = 0
            state.character.light = 0
            state.character.darkness = 0
        }

        state.attributePoints.sort(function(a,b) {
            return a - b;
        });

        if (state.character.skills.length > 0) {
            state.character.skills.map(skillItem => {
                if(skillItem.training_level > 1) {
                    const refundSkillPoint = skillItem.training_level -1
                    state.spentSkillPoints = state.spentSkillPoints - refundSkillPoint
                    skillItem.training_level = 1
                }
            })
        }
    },
    setSelectAffinityLevel (state, affinityChoice) {
        if (affinityChoice.level == state.character[affinityChoice.affinity]) {
            state.character[affinityChoice.affinity] = state.character[affinityChoice.affinity] -1
            state.affinityPoints[affinityChoice.attribute] = state.affinityPoints[affinityChoice.attribute] +1

        } else {
            state.affinityPoints[affinityChoice.attribute] = state.affinityPoints[affinityChoice.attribute] - (affinityChoice.level - state.character[affinityChoice.affinity])
            state.character[affinityChoice.affinity] = affinityChoice.level
        }

        if (state.character.skills.length > 0) {
            state.character.skills.map(skillItem => {
                if(skillItem.training_level > 1) {
                    const refundSkillPoint = skillItem.training_level -1
                    state.spentSkillPoints = state.spentSkillPoints - refundSkillPoint
                    skillItem.training_level = 1
                }
            })
        }
        
    },
    setSourceSkills (state, { sourceSkills, primarySpecialization, secondarySpecialization } ) {
        let setPrimarySpecialization = []

        const primarySkillSpecialization = sourceSkills.primary_skill.specialization

        for (let specialization in primarySkillSpecialization) {
            const specializationId = primarySkillSpecialization[specialization].id
            
            if (specializationId == primarySpecialization) {
                setPrimarySpecialization = primarySkillSpecialization[specialization]
                state.specializations.push(setPrimarySpecialization)
            }
        }

        let primarySkillId = ''

        if (setPrimarySpecialization.length === 0) {
            primarySkillId = sourceSkills.primary_skill.id
        } else {
            primarySkillId = sourceSkills.primary_skill.id + '-' + setPrimarySpecialization.id
        }

        const primarySkill = {
            id: primarySkillId,
            name: sourceSkills.primary_skill.name,
            attribute: sourceSkills.primary_skill.attribute,
            attribute_label: sourceSkills.primary_skill.attribute_label,
            affinity: sourceSkills.primary_skill.affinity,
            affinity_label: sourceSkills.primary_skill.affinity_label,
            description: sourceSkills.primary_skill.description,
            specialization: setPrimarySpecialization,
            actions: sourceSkills.primary_skill.actions,
            traits: sourceSkills.primary_skill.traits,
            armor_penality: sourceSkills.primary_skill.armor_penality,
            training_level: 1,
            source_skill: true  
        }

        let setSecondarySpecialization = []

        const secondarySkillSpecialization = sourceSkills.secondary_skill.specialization

        for (let specialization in secondarySkillSpecialization) {
            const specializationId = secondarySkillSpecialization[specialization].id
            
            if (specializationId == secondarySpecialization) {
                setSecondarySpecialization = secondarySkillSpecialization[specialization]
                state.specializations.push(setSecondarySpecialization)
            }
        }

        let secondarySkillId = ''

        if (setSecondarySpecialization.length === 0) {
            secondarySkillId = sourceSkills.secondary_skill.id
        } else {
            secondarySkillId = sourceSkills.secondary_skill.id + '-' + setSecondarySpecialization.id
        }       

        const secondarySkill = {
            id: secondarySkillId,
            name: sourceSkills.secondary_skill.name,
            attribute: sourceSkills.secondary_skill.attribute,
            attribute_label: sourceSkills.secondary_skill.attribute_label,
            affinity: sourceSkills.secondary_skill.affinity,
            affinity_label: sourceSkills.secondary_skill.affinity_label,
            description: sourceSkills.secondary_skill.description,
            specialization: setSecondarySpecialization,
            actions: sourceSkills.secondary_skill.actions,
            traits: sourceSkills.secondary_skill.traits,
            armor_penality: sourceSkills.secondary_skill.armor_penality,
            training_level: 1,
            source_skill: true  
        }

        const checkSourceSkills = state.character.skills.filter(skill => skill.source_skill === true)
        const checkPrimarySkill = state.character.skills.filter(skill => skill.id === primarySkill.id)
        const checkSecondarySkill = state.character.skills.filter(skill => skill.id === secondarySkill.id)

        if (checkSourceSkills.length > 0) {
            for (let sourceSkill in checkSourceSkills) {
                const skills = state.character.skills
                skills.splice(skills.findIndex(index => index.name === checkSourceSkills[sourceSkill].name),1)   

                state.skillDetail = []
            }
        }  
        
        if (checkPrimarySkill.length > 0) {
            const skills = state.newCharacter.skills
            skills.splice(skills.findIndex(index => index.name === checkPrimarySkill.name),1)
            state.spentSkillPoints = state.spentSkillPoints - checkPrimarySkill[0].training_level

        } 
        
        if (checkSecondarySkill.length > 0) {
            const skills = state.newCharacter.skills
            skills.splice(skills.findIndex(index => index.id === checkSecondarySkill.name),1)
            state.spentSkillPoints = state.spentSkillPoints - checkSecondarySkill[0].training_level            
        }

        state.character.skills.push(primarySkill)
        state.character.skills.push(secondarySkill)
    },
    setSkills (state,  { skill, specializationSelected } ) {
        const specializationList = skill.specialization
        
        let setSpecialization = []

        for (let specialization in specializationList) {
            const specializationId = specializationList[specialization].id

            if (specializationId == specializationSelected) {
                setSpecialization = specializationList[specialization]
                state.specializations.push(setSpecialization)
            }
        }

        let skillId = ''

        if (setSpecialization.length === 0) {
            skillId = skill.id
        } else {
            skillId = skill.id + '-' + setSpecialization.id
        }

        const newSkill = {
            id: skillId,
            name: skill.name,
            attribute: skill.attribute,
            attribute_label: skill.attribute_label,
            affinity: skill.affinity,
            affinity_label: skill.affinity_label,
            description: skill.description,
            specialization: setSpecialization,
            actions: skill.actions,
            traits: skill.traits,
            armor_penality: skill.armor_penality,
            training_level: 1,
            source_skill: false    
        }
        state.character.skills.push(newSkill)
    },
    setSpentSkillPoints (state) {
        state.spentSkillPoints = state.spentSkillPoints +1
    },
    setAddSkillPoints (state) {
        state.spentSkillPoints = state.spentSkillPoints -1
    },
    setSkillDetail (state, id) {
        if (state.skillDetail.id === id ) {
            state.skillDetail = []
        } else {
            state.skillDetail = state.character.skills.find(skill => skill.id === id)
        }  
    },
    setClearSkillDetail (state) {
        state.skillDetail = []
    },
    setHeritageAbility (state, path) {
        state.character.heritageAbilities = []

        for (let ability in path.abilities) {

            let trained = false

            if (path.abilities[ability].initial) {
                trained = true
            }

            const trainedAbility = {
                activation: path.abilities[ability].activation,
                cost: path.abilities[ability].cost,
                description: path.abilities[ability].description,
                id: path.abilities[ability].id,
                initial: path.abilities[ability].initial,
                name: path.abilities[ability].name,
                requirement: path.abilities[ability].requirement,
                resource: path.abilities[ability].resource,
                traits: path.abilities[ability].traits,
                trained: trained,
                path: path.id,
                // heritage: true
                source: path.abilities[ability].source,
            }

            state.character.heritageAbilities.push(trainedAbility)
        }
    },
    setSourceAbility (state, path) {
        state.character.sourceAbilities = []

        for (let ability in path) {
            
            const trainedAbility = {
                activation: path[ability].activation,
                cost: path[ability].cost,
                description: path[ability].description,
                id: path[ability].id,
                initial: path[ability].initial,
                name: path[ability].name,
                requirement: path[ability].requirement,
                resource: path[ability].resource,
                traits: path[ability].traits,
                trained: false,
                path: path.id,
                source: path[ability].source,
            }

            state.character.sourceAbilities.push(trainedAbility)
        }
    },
    setAbility (state, ability) {      
        let abilityList = []

        if (ability.source.name == 'Herança') {
            abilityList = state.character.heritageAbilities
        } else if (ability.source.name == 'Origem') {
            abilityList = state.character.sourceAbilities
        } else if (ability.source.name == 'Geral') {
            abilityList = state.pathSelected[0].abilities
            state.character.abilities = abilityList
        } else {
            abilityList = state.magicPathSelected[0].abilities
            state.character.magicalAbilities = abilityList
        }

        abilityList.map(abilityItem => {
            if (abilityItem.id == ability.id) {

                ability.trained = !abilityItem.trained

                if (ability.trained) {
                    if (ability.source.name == 'Herança') {
                        state.maxHeritageAbilities = state.maxHeritageAbilities -1 
                    } else if (ability.source.name == 'Origem') {
                        state.maxSourceAbilities = state.maxSourceAbilities -1
                    } else if (ability.source.name == 'Geral') {
                        state.maxPathAbilities = state.maxPathAbilities -1
                    } else {
                        state.maxPathMagicAbilities = state.maxPathMagicAbilities -1
                    }

                    if (ability.sorcery) {
                        state.sorcery = true
                    }
                } else {
                    if (ability.source.name == 'Herança') {
                        state.maxHeritageAbilities = state.maxHeritageAbilities +1 
                    } else if (ability.source.name == 'Origem') {
                        state.maxSourceAbilities = state.maxSourceAbilities +1
                    } else if (ability.source.name == 'Geral') {
                        state.maxPathAbilities = state.maxPathAbilities +1
                    } else {
                        state.maxPathMagicAbilities = state.maxPathMagicAbilities +1
                    }
                    
                    if (ability.sorcery) {
                        state.sorcery = false
                    }
                }
            }
            return abilityItem
        })     
    },
    setSelectPath (state, path) {
        if (state.pathSelected.length > 0) {
            state.pathSelected = []
            state.character.abilities = []

            if (state.maxPathAbilities === 0) {
                state.maxPathAbilities = state.maxPathAbilities +1
            }   
        }
        state.pathSelected = path
        state.sorcery = false
        state.character.spells = []    
        state.maxSpells = 3
    },
    setClearSelectedPath (state) {
        state.pathSelected = []
        state.character.abilities = []
        state.sorcery = false
        
        if (state.maxPathAbilities === 0) {
            state.maxPathAbilities = state.maxPathAbilities +1
        }  
    },
    setSelectMagicPath (state, magicPath) {
        if (state.magicPathSelected.length > 0) {
            state.magicPathSelected = []
            state.character.magicalAbilities = []

            if (state.maxPathMagicAbilities === 0) {
                state.maxPathMagicAbilities = state.maxPathMagicAbilities +1
            }
        }
        state.magicPathSelected = magicPath
    },
    setClearSelectedMagicPath (state) {
        state.magicPathSelected = []
        state.character.magicalAbilities = []
    },
    setInitialArmors (state, initialArmors) {
        let armorList = []

        for (let armor in initialArmors) {
            const newArmor = {
                armor_penality: initialArmors[armor].armor_penality,
                cost: initialArmors[armor].cost,
                damage_resistance: initialArmors[armor].damage_resistance,
                description: initialArmors[armor].description,
                id: initialArmors[armor].id,
                initial: initialArmors[armor].initial,
                material: initialArmors[armor].material,
                name: initialArmors[armor].name,
                traits: initialArmors[armor].traits,
                weight: initialArmors[armor].weight,
                weight_stack: initialArmors[armor].weight_stack,
                trained: false
            }

            armorList.push(newArmor)
        }

        state.initialArmors = armorList
    },
    setInitialShields (state, shields) {
        let shieldList = []

        for (let shield in shields) {
            const newShield = {
                armor_penality: shields[shield].armor_penality,
                cost: shields[shield].cost,
                damage_resistance: shields[shield].damage_resistance,
                defense_bonus: shields[shield].defense_bonus,
                description: shields[shield].description,
                id: shields[shield].id,
                life_points: shields[shield].life_points,
                name: shields[shield].name,
                traits: shields[shield].traits,
                weight: shields[shield].weight,
                weight_stack: shields[shield].weight_stack,
                trained: false
            }

            shieldList.push(newShield)
        }

        state.initialShields = shieldList
    },
    setInitialWeapons (state, weapons) {        
        let weaponList = []
        
        for (let groups in weapons) {
            const weaponGroup = weapons[groups]

            for (let weapon in weaponGroup) {
                const newWeapon = {
                    body: weaponGroup[weapon].body,
                    cost: weaponGroup[weapon].cost,
                    damage: weaponGroup[weapon].damage,
                    description: weaponGroup[weapon].description,
                    id: weaponGroup[weapon].id,
                    name: weaponGroup[weapon].name,
                    precision: weaponGroup[weapon].precision,
                    range: weaponGroup[weapon].range,
                    specialization: weaponGroup[weapon].specialization,
                    traits: weaponGroup[weapon].traits,
                    weight: weaponGroup[weapon].weight,
                    weight_stack: weaponGroup[weapon].weight_stack,
                    acquired: false
                }

                weaponList.push(newWeapon)
            }
        }
        
        state.initialWeapons = weaponList
    },
    setChooseArmor (state, armor) {
        let armorList = state.initialArmors

        armorList.map(armorItem => {
            if (armorItem.id == armor.id) {
                armorItem.acquired = !armorItem.acquired

                const armorList = state.character.equipments.armors
                const filter = armorList.find(equipment => equipment.id === armor.id)

                if (armorItem.acquired) {
                    state.maxInitialArmor = state.maxInitialArmor -1
                    
                    if (!filter) {
                        const newArmor = {
                            id: armor.id,
                            name: armor.name,
                            type: "armors",
                            quantity: 1,
                            initial: true
                        }
    
                        state.character.equipments.armors.push(newArmor) 
                    } 
                } else {
                    state.maxInitialArmor = state.maxInitialArmor +1

                    if(filter.quantity > 1) {
                        filter.quantity = filter.quantity -1
                        filter.initial = false 
                    } else {
                        armorList.splice(armorList.findIndex(index => index.id === armor.id),1) 
                    }
                      
                }
            }
            return armorItem
        })
    },
    setChooseShield (state, shield) {
        let shieldList = state.initialShields

        shieldList.map(shieldItem => {
            if (shieldItem.id == shield.id) {
                shieldItem.acquired = !shieldItem.acquired

                const shieldList = state.character.equipments.shields
                const filter = shieldList.find(equipment => equipment.id === shield.id)

                if (shieldItem.acquired) {
                    state.maxInitialShield = state.maxInitialShield -1                    

                    if (!filter) {
                        const newShield = {
                            id: shield.id,
                            name: shield.name,
                            type: "shields",
                            quantity: 1,
                            initial: true
                        }
    
                        state.character.equipments.shields.push(newShield) 
                    }
                } else {
                    state.maxInitialShield = state.maxInitialShield +1

                    if(filter.quantity > 1) {
                        filter.quantity = filter.quantity -1
                        filter.initial = false 
                    } else {
                        shieldList.splice(shieldList.findIndex(index => index.id === shield.id),1) 
                    }
                }
            }
            return shieldItem
        })
    },
    setChooseWeapon (state, weapon) {
        let weaponList = state.initialWeapons

        weaponList.map(weaponItem => {
            if (weaponItem.id == weapon.id) {
                weaponItem.acquired = !weaponItem.acquired

                const weaponList = state.character.equipments.weapons
                const filter = weaponList.find(equipment => equipment.id === weapon.id)

                if (weaponItem.acquired) {
                    state.maxInitialWeapons = state.maxInitialWeapons -1 

                    if (!filter) {
                        const newWeapon = {
                            id: weapon.id,
                            name: weapon.name,
                            type: "weapons",
                            quantity: 1,
                            initial: true
                        }
    
                        state.character.equipments.weapons.push(newWeapon) 
                    }
                } else {
                    state.maxInitialWeapons = state.maxInitialWeapons +1

                    if(filter.quantity > 1) {
                        filter.quantity = filter.quantity -1
                        filter.initial = false 
                    } else {
                        weaponList.splice(weaponList.findIndex(index => index.id === weapon.id),1)   
                    }                    
                }
            }
            return weaponItem
        })
    },   
    setGeo (state) {
        state.character.geo = Math.floor(Math.random() * (30 - 5)) + 5;
    },
    setEquipmentBuy (state, { equipment, equipmentCategory} ) {
        const newEquipment = {
            id: equipment.id,
            name: equipment.name,
            type: equipmentCategory,
            quantity: 1
        }

        if (state.character.equipments[equipmentCategory].length > 0) {
            const category = state.character.equipments[equipmentCategory]
            
            const findEquipment = category.find(el => {
                if (el.id === equipment.id) {
                    return el
                }
            })

            if (findEquipment) { 
                findEquipment.quantity = findEquipment.quantity +1
            } else {
                state.character.equipments[equipmentCategory].push(newEquipment)
            }

            state.notification.message = 'Você comprou ' + newEquipment.name + '.'
            state.notification.type = 'success'
            state.notification.state = true
        } else {
            state.character.equipments[equipmentCategory].push(newEquipment)
            state.notification.message = 'Você comprou ' + newEquipment.name + '.'
            state.notification.type = 'success'
            state.notification.state = true
        }

        state.character.geo = state.character.geo - equipment.cost
    },
    setIncreaseEquipment (state, { equipment, equipmentResult } ) {
        const category = state.character.equipments[equipment.type]

        const findEquipment = category.find(el => {
            if (el.id === equipment.id) {
                return el
            }
        })

        if (findEquipment) {
            if (state.character.geo >= equipmentResult[0].cost) {
                findEquipment.quantity = findEquipment.quantity +1
                
                state.character.geo = state.character.geo - equipmentResult[0].cost
                
                state.notification.message = 'Você comprou 1 ' + equipment.name + '.'
                state.notification.type = 'success'
                state.notification.state = true
            } else {
                state.notification.message = 'Você não possui G$' + equipmentResult[0].cost + ' para fazer essa compra.'
                state.notification.type = 'error'
                state.notification.state = true
            }
        }
    },
    setSellEquipment (state, { equipment, equipmentResult } ) {
        const category = state.character.equipments[equipment.type]

        const findEquipment = category.find(el => {
            if (el.id === equipment.id) {
                return el
            }
        })

        if (findEquipment.initial) {
            if (findEquipment.quantity > 1) {
                state.character.geo = state.character.geo + equipmentResult[0].cost
            } else {
                findEquipment.acquired = false
                              
                if (equipment.type === 'armors') {
                    const findIndex = state.initialArmors.findIndex(index => index.id === equipment.id)
                    state.initialArmors[findIndex].acquired = false
                    state.maxInitialArmor = state.maxInitialArmor +1
                } else if (equipment.type === 'shields') {
                    const findIndex = state.initialShields.findIndex(index => index.id === equipment.id)
                    state.initialShields[findIndex].acquired = false
                    state.maxInitialShield = state.maxInitialShield +1
                } else if (equipment.type === 'weapons') {
                    const findIndex = state.initialWeapons.findIndex(index => index.id === equipment.id)
                    state.initialWeapons[findIndex].acquired = false
                    state.maxInitialWeapons = state.maxInitialWeapons +1
                }      
            }
        } else {
            state.character.geo = state.character.geo + equipmentResult[0].cost
        }

        if (findEquipment) { 
            findEquipment.quantity = findEquipment.quantity -1

            state.notification.message = 'Você vendeu 1 ' + equipment.name + '.'
            state.notification.type = 'success'
            state.notification.state = true
            
            if (findEquipment.quantity === 0) {
                category.splice(category.findIndex(index => index.id === equipment.id),1)

                state.notification.message = 'O item ' + equipment.name + ' foi removido do seu inventário.'
                state.notification.type = 'success'
                state.notification.state = true
            }
        }
    },
    setLearnSpell (state, spell) {
        state.character.spells.push(spell)
        state.maxSpells = state.maxSpells -1 
    },
    setDeleteSpell (state, spell) {
        state.character.spells.splice(state.character.spells.findIndex(index => index.id === spell), 1)
        state.maxSpells = state.maxSpells +1 
    },
    setClearNotification (state) {
        state.notification.message = '',
        state.notification.type = '',
        state.notification.state = false
    },
    setClearNewCharacter (state) {
        Object.assign(state.character, characterTemplate())
        state.maxHeritageAbilities = 2,
        state.maxSourceAbilities = 1,
        state.maxPathAbilities = 1,
        state.maxPathMagicAbilities = 1,
        state.maxInitialWeapons = 2,
        state.maxInitialArmor = 1,
        state.maxInitialShield = 1,
        state.pathSelected = [],
        state.magicPathSelected = [],
        state.initialArmors = [],
        state.initialShields = [],
        state.initialWeapons = [],
        state.equipmentList = []
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}