import api from "@/plugins/axios";

const state = () => ({
    sourceInfo: [],
    sourceSelected: false,
    sourceList: [],  
})

const getters = {}

const actions = {
    async getSources ({ commit }) {
        const sources = await api.get('/sources/?fields=id,name')
        commit('setSources', sources.data.results)
    },
    async getSourceInfo ({ commit }, id) {
        const sourceInfo = await api.get('/sources/?id=' + id)
        commit('setSourceInfo', sourceInfo.data.results)
    },
    getSourceState ({ commit }, sourceState) {
        commit('setSourceSelected', sourceState)
    }
}

const mutations = {
    setSources (state, sources) {
        state.sourceList = sources
    },
    setSourceInfo (state, sourceInfo) {
        state.sourceInfo = sourceInfo
    },
    setSourceSelected (state, sourceState) {
        state.sourceSelected = sourceState
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}