<script>
    import Section from '@/components/template/Section.vue'
    import Divider from '@/components/template/Divider.vue'
    import Activation from '@/components/characters/Activation.vue'
    import { mapState } from 'vuex'
    import api from "@/plugins/axios";

    export default {
        name: 'ShowCharacter',
        components: {
            Section, Divider, Activation
        },
        data() {
            return {
                heritage: [],
                source: [],
                motivation: [],
                skills: [],
                abilities: [],
                armors: [],
                weapons: [],
                shields: [],
                items: [],
                spells: [],
                enchants: []
            }
        },
        computed: {
            ...mapState({
                userId: state => state.login.userId,
                showCharacter: state => state.characters.showCharacter
            }),
        },
        methods: {
            back() {
                this.$router.push("/characters")              
            }
        },
        watch: {
            showCharacter() {
                api.get('/heritages/?id=' +this.showCharacter.heritage).then(response => {
                    this.heritage = response.data.results
                })

                api.get('/sources/?id=' +this.showCharacter.source).then(response => {
                    this.source = response.data.results
                })

                api.get('/motivations/?id=' +this.showCharacter.motivation).then(response => {
                    this.motivation = response.data.results
                })

                // Perícias

                const skillToJson = JSON.parse(this.showCharacter.skills)

                for (let skill in skillToJson) {
                    api.get('/skills/?id=' +skillToJson[skill].id).then(response => {
                        const findSpecialization = response.data.results[0].specialization.find(specialization => specialization.id === skillToJson[skill].specialization)

                        const newSkill = {
                            affinity: response.data.results[0].affinity_label,
                            armor_penality: response.data.results[0].armor_penality,
                            description: response.data.results[0].description,
                            name: response.data.results[0].name,
                            specialization: findSpecialization,
                            trainingLevel: skillToJson[skill].trainingLevel,
                        }
                        
                        this.skills.push(newSkill)
                    })
                }

                // Abilidades

                const abilitiesToJson = JSON.parse(this.showCharacter.abilities)
                
                for (let ability in abilitiesToJson) {
                    api.get('/abilitys/?id=' +abilitiesToJson[ability]).then(response => {
                        const newAbility = {
                            activation: response.data.results[0].activation,
                            cost: response.data.results[0].cost,
                            description: response.data.results[0].description,
                            initial: response.data.results[0].initial,
                            name: response.data.results[0].name,
                            requirement: response.data.results[0].requirement,
                            resource: response.data.results[0].resource,
                            source: response.data.results[0].source
                        }

                        this.abilities.push(newAbility)
                    })
                }

                // Equipamentos

                const equipmentsToJson = JSON.parse(this.showCharacter.equipments)

                for (let equipment in equipmentsToJson) {
                    for (let item in equipmentsToJson[equipment]) {
                        api.get('/' + equipmentsToJson[equipment][item].type + '/?id=' +equipmentsToJson[equipment][item].id).then(response => {
                            if (equipmentsToJson[equipment][item].type === 'armors') {
                                const armor = {
                                    armor_penality: response.data.results[0].armor_penality,
                                    cost: response.data.results[0].cost,
                                    damage_resistance: response.data.results[0].damage_resistance,
                                    description: response.data.results[0].description,
                                    name: response.data.results[0].name,
                                    weight: response.data.results[0].weight,
                                    weight_stack: response.data.results[0].weight_stack,
                                    quantity: equipmentsToJson[equipment][item].quantity
                                }
                                this.armors.push(armor)
                            } else if (equipmentsToJson[equipment][item].type === 'weapons') {
                                const weapon = {
                                    body: response.data.results[0].body,
                                    cost: response.data.results[0].cost,
                                    damage: response.data.results[0].damage,
                                    name: response.data.results[0].name,
                                    precision: response.data.results[0].precision,
                                    range: response.data.results[0].range,
                                    weight: response.data.results[0].weight,
                                    weight_stack: response.data.results[0].weight_stack  ,
                                    quantity: equipmentsToJson[equipment][item].quantity                             }
                                this.weapons.push(weapon)
                            } else if (equipmentsToJson[equipment][item].type === 'shields') {
                                const shield = {
                                    name: response.data.results[0].name,
                                    quantity: equipmentsToJson[equipment][item].quantity
                                }
                                this.shields.push(shield)

                            } else if (equipmentsToJson[equipment][item].type === 'items') {
                                console.log(equipmentsToJson[equipment][item])
                                const newItem = {
                                    category: response.data.results[0].category.name,
                                    cost: response.data.results[0].cost,
                                    name: response.data.results[0].name,
                                    weight: response.data.results[0].weight,
                                    weight_stack: response.data.results[0].weight_stack,
                                    quantity: equipmentsToJson[equipment][item].quantity
                                }
                                this.items.push(newItem)
                            }
                        })
                    }
                }
            }
        },
        created() {
            this.$store.dispatch('characters/showCharacter', this.$route.params.id)
        }
    }
</script>

