<template>
    <div>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>

        <div>
            <Section name="Crie seu Aventureiro" />
        </div>

        <Divider :step=1 title="Escolha Características Básicas" />

        <div class="level">
            <p>Nível {{ level }}</p>
        </div>

        <div class="flex">                
            <div class="portrait">
                <img src="@/assets/portrait.png" alt="Foto">
            </div>

            <div>
                <div class="flex">
                    <p v-if="size"><strong>Tamanho</strong> {{ size }}</p>
                    
                    <!-- <p v-if="move > 0"><strong>Movimento</strong> {{ move + agility }}</p> -->
                </div>              

                <div class="flex">
                    <Input id="name" type="text" label="Nome" :value="name" model="update:name" v-model:name="name" />
                    <Input id="lastName" type="text" label="Sobrenome" :value="lastName" model="update:lastName" v-model:lastName="lastName" />
                    <Select id="gender" label="Genero" model="update:gender" v-model:gender="gender">
                        <option></option>
                        <option v-for="gender in genderList" :key="gender.id" :value="gender.id">{{ gender.name }}</option>
                    </Select>
                </div>

                <div class="flex">
                    <Input id="age" type="number" label="Idade" :value="age" model="update:age" v-model:age="age" />
                    <Input id="height" type="number" label="Altura" :value="height" model="update:height" v-model:height="height" />
                    <Input id="weight" type="number" label="Peso" :value="weight" model="update:weight" v-model:weight="weight" />
                </div>
            </div>
        </div>

        <Divider :step=2 title="Escolha Herança, Origem e Motivação" />

        <div class="flex">
            <ChoiceLink title="Escolha sua Herança" to="/create-character/heritages" :value="heritage" />
            <ChoiceLink title="Escolha sua Origem" to="/create-character/sources" :value="source" :impulse="true" />
            <ChoiceLink title="Escolha sua Motivação" to="/create-character/motivations" :value="motivation" :impulse="true" />
        </div>

        <Attributes />

        <Skills />
        
        <Ability />

        <Equipment />

        <Spells v-if="sorcery" /> 

        <button @click="saveCharacter">Salvar Personagem</button>

        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in">
            <Alert type="error" v-if="errors.length">
                <p v-for="(error, index) in errors" :key="index">{{ error }}</p>
            </Alert>
        </transition>
    </div>
</template>

<script>
    import { mapState } from 'vuex'
    
    import api from "@/plugins/axios";

    import Divider from '@/components/template/Divider.vue'
    import Alert from '@/components/template/Alert.vue'
    import Section from '@/components/template/Section.vue'
    
    import Ability from '@/components/characters/Ability.vue'
    import Attributes from '@/components/characters/Attributes.vue'
    import ChoiceLink from '@/components/characters/ChoiceLink.vue'
    import Skills from '@/components/characters/Skills.vue'
    import Equipment from '@/components/characters/Equipment.vue'
    import Spells from '@/components/characters/Spells.vue'
    
    
    import Input from '@/components/forms/Input.vue'
    import Select from '@/components/forms/Select.vue'
    
    
    export default {
        name: 'CreateCharacters',
        components: {
            Divider, Alert, Section, Ability, Attributes, ChoiceLink, Equipment, Skills, Input, Select, Spells
        },
        data() {
            return {
                name: '',
                lastName: '',
                gender: '',
                age: '',
                height: '',
                weight: '',
                errors: []
            }
        },
        computed: {
            ...mapState({
                username: state => state.login.userId,
                character: state => state.characters.character,
                level: state => state.characters.character.level,
                size: state => state.characters.size,
                move: state => state.characters.move,
                genderList: state => state.characters.genderList,
                heritage: state => state.characters.character.heritage,
                source: state => state.characters.character.source,
                motivation: state => state.characters.character.motivation,              
                body: state => state.characters.character.body,
                agility: state => state.characters.character.agility,
                dexterity: state => state.characters.character.dexterity,
                strength: state => state.characters.character.strength,
                stamina: state => state.characters.character.stamina,
                mind: state => state.characters.character.mind,
                cunning: state => state.characters.character.cunning,
                influence: state => state.characters.character.influence,
                perception: state => state.characters.character.perception,
                will: state => state.characters.character.will,
                ether: state => state.characters.character.ether,
                water: state => state.characters.character.water,
                fire: state => state.characters.character.fire,
                air: state => state.characters.character.air,
                earth: state => state.characters.character.earth,
                moon: state => state.characters.character.moon,
                nature: state => state.characters.character.nature,
                light: state => state.characters.character.light,
                darkness: state => state.characters.character.darkness,
                skills: state => state.characters.character.skills,
                heritageAbilities: state => state.characters.character.heritageAbilities,
                sourceAbilities: state => state.characters.character.sourceAbilities,
                abilities: state => state.characters.character.abilities,
                magicalAbilities: state => state.characters.character.magicalAbilities,
                equipments: state => state.characters.character.equipments,
                maxHeritageAbilities: state => state.characters.maxHeritageAbilities,
                maxSourceAbilities: state => state.characters.maxSourceAbilities,
                maxPathAbilities: state => state.characters.maxPathAbilities,
                maxPathMagicAbilities: state => state.characters.maxPathMagicAbilities,
                maxInitialArmor: state => state.characters.maxInitialArmor,
                spentSkillPoints: state => state.characters.spentSkillPoints,
                geo: state => state.characters.character.geo,
                sorcery: state => state.characters.sorcery,
                maxSpells: state => state.characters.maxSpells,
                maxEnchants: state => state.characters.maxEnchants,
                attributePoints: state => state.characters.attributePoints,
                affinityPoints: state => state.characters.affinityPoints,
                spells: state => state.characters.character.spells,
                enchants: state => state.characters.character.enchants
            }),
            skillPoints() {
                return (this.mind *5) + this.cunning
            }
        },
        methods: {
            back() {
                if (confirm('Deseja sair da criação de personagens? Os dados serão perdidos!')) {
                    this.$store.dispatch('characters/clearNewCharacter')
                    this.$router.push("/characters")
                }                
            },
            checkErrors() {
                this.errors = []

                if(!this.name) {
                    this.errors.push(`O campo "Nome" é obrigatório.`)
                }

                if(this.heritage.length === 0) {
                    this.errors.push(`O campo "Escolha sua Herança" é obrigatório.`)
                } else {
                    if(this.maxHeritageAbilities > 0) {
                        this.errors.push(`Você ainda tem que escolher ` + this.maxHeritageAbilities + ` habilidade(s) de herança.`)
                    }
                }

                if(this.source.length === 0) {
                    this.errors.push(`O campo "Escolha sua Origem" é obrigatório.`)
                } else {
                    if(this.maxSourceAbilities > 0) {
                        this.errors.push(`Você ainda tem que escolher ` + this.maxSourceAbilities + ` habilidade de origem.`)
                    }
                }

                if(this.motivation.length === 0) {
                    this.errors.push(`O campo "Escolha sua Motivação" é obrigatório.`)
                }

                if(this.attributePoints.length > 0) {
                    this.errors.push(`Você ainda tem ` + this.attributePoints.length + ` atributo(s) para distribuir.`)
                }

                if(this.affinityPoints.body > 0) {
                    this.errors.push(`Você ainda tem ` + this.affinityPoints.body + ` pontos para distribuir nas afinidades de Corpo.`)
                }

                if(this.affinityPoints.mind > 0) {
                    this.errors.push(`Você ainda tem ` + this.affinityPoints.mind + ` pontos para distribuir nas afinidades de Mente.`)
                }

                if(this.affinityPoints.ether > 0) {
                    this.errors.push(`Você ainda tem ` + this.affinityPoints.ether + ` pontos para distribuir nas afinidades de Ether.`)
                }

                if(this.skillPoints != this.spentSkillPoints) {
                    const skillPoints = this.skillPoints - this.spentSkillPoints
                    this.errors.push(`Você ainda tem ` + skillPoints + ` ponto(s) de perícia restantes.`)
                }
                
                if(this.maxPathAbilities > 0) {
                    this.errors.push(`Você ainda tem que escolher ` + this.maxPathAbilities + ` habilidade geral.`)
                }
                
                if(this.maxPathMagicAbilities > 0) {
                    this.errors.push(`Você ainda tem que escolher ` + this.maxPathMagicAbilities + ` habilidade mágica.`)
                }
                
                if(this.maxInitialArmor > 0) {
                    this.errors.push(`Você ainda tem que escolher ` + this.maxInitialArmor + ` armadura.`)
                }

                if(this.sorcery) {
                    if(this.maxSpells != 0) {
                        this.errors.push(`Você ainda tem que aprender ` + this.maxSpells + ` sortilégio(s).`)
                    }

                    if(this.maxEnchants != 0) {
                        this.errors.push(`Você ainda tem que aprender ` + this.maxEnchants + ` encanto.`)
                    }
                }  
            },
            saveCharacter() {
                this.checkErrors()

                if (this.errors.length) {
                    setTimeout(() => {
                        this.errors = []
                    }, 4000)
                } else {
                    // # Generate Skill List
                    let skillList = []

                    for (let skill in this.skills) {
                        if (this.skills[skill].specialization.length === 0) {
                            const newSkill = {
                                id: this.skills[skill].id,
                                specialization: [],
                                sourceSkill: this.skills[skill].source_skill,
                                trainingLevel: this.skills[skill].training_level
                            }

                            skillList.push(newSkill)
                        } else {
                            let skillId = this.skills[skill].id

                            const newSkill = {
                                id: Number(skillId.slice(0,1)),
                                specialization: this.skills[skill].specialization.id,
                                sourceSkill: this.skills[skill].source_skill,
                                trainingLevel: this.skills[skill].training_level
                            }

                            skillList.push(newSkill)
                        }
                    }

                    // # Generate Ability List
                    let abilityList = []

                    // ## Pick Heritage Abilities
                    for (let ability in this.heritageAbilities) {                        
                        if (this.heritageAbilities[ability].initial === true || this.heritageAbilities[ability].trained === true ) {
                            abilityList.push(this.heritageAbilities[ability].id)
                        }
                    }

                    // ## Pick Source Abilities
                    for (let ability in this.sourceAbilities) {                   
                        if (this.sourceAbilities[ability].initial === true || this.sourceAbilities[ability].trained === true ) {
                            abilityList.push(this.sourceAbilities[ability].id)
                        }
                    }

                    // ## Pick General Abilities
                    for (let ability in this.abilities) {                   
                        if (this.abilities[ability].initial === true || this.abilities[ability].trained === true ) {
                            abilityList.push(this.abilities[ability].id)
                        }
                    }

                    // ## Pick Magical Abilities
                    for (let ability in this.magicalAbilities) {                   
                        if (this.magicalAbilities[ability].initial === true || this.magicalAbilities[ability].trained === true ) {
                            abilityList.push(this.magicalAbilities[ability].id)
                        }
                    }

                    const newCharacter = {
                        username: this.username,
                        name: this.name,
                        heritage: this.heritage.id,
                        source: this.source.id,
                        motivation: this.motivation.id,
                        body: this.body,
                        agility: this.agility,
                        dexterity: this.dexterity,
                        strength: this.strength,
                        stamina: this.stamina,
                        mind: this.mind,
                        cunning: this.cunning,
                        influence: this.influence,
                        perception: this.perception,
                        will: this.will,
                        ether: this.ether,
                        water: this.water,
                        fire: this.fire,
                        air: this.air,
                        earth: this.earth,
                        moon: this.moon,
                        nature: this.nature,
                        light: this.light,
                        darkness: this.darkness,
                        skills: JSON.stringify(skillList),
                        abilities: JSON.stringify(abilityList),
                        equipments: JSON.stringify(this.equipments),
                        spells: JSON.stringify(this.spells),
                        enchants: JSON.stringify(this.enchants),
                        geo: this.geo
                    }

                    api.post('/characters/', newCharacter).then(response => {
                        if (response.status == '201') {
                            this.$store.dispatch('characters/clearNewCharacter')
                            this.$router.push("/characters")
                        }
                    })

                    skillList = []
                    abilityList = []
                }                
            }
        },
        created() {
            if (this.genderList.length === 0) {
                this.$store.dispatch('characters/getGenders')
            }

            if (this.geo === 0) {
                this.$store.dispatch('characters/getGeo')
            }
        }
    }
</script>

<style scoped>
    .flex {
        align-items: center;
        display: flex;
        margin-bottom: 32px;
    }

    .flex > p {
        margin-right: 32px;
    }
    .portrait {
        background-color: #fff;
        border: 1px solid #d5d5d5;
        border-radius: 50%;
        cursor: pointer;
        margin-right: 32px;
        padding: 8px;

        -webkit-transition: background-color 0.5s ease-in-out;
        -moz-transition: background-color 0.5s ease-in-out;
        -ms-transition: background-color 0.5s ease-in-out;
        -o-transition: background-color 0.5s ease-in-out;
        transition: background-color 0.5s ease-in-out;
    }
    .portrait:hover {
        background-color: #d5d5d5;

        -webkit-transition: background-color 0.5s ease-in-out;
        -moz-transition: background-color 0.5s ease-in-out;
        -ms-transition: background-color 0.5s ease-in-out;
        -o-transition: background-color 0.5s ease-in-out;
        transition: background-color 0.5s ease-in-out;
    }
    .portrait img {
        background-color: #fff;
        border: 1px solid #d5d5d5;
        border-radius: 50%;
        height: 250px;
        width: 250px;
    }
    .level {
        display: flex;
        justify-content: end;
    }
    .level p {
        border: none;
        border-bottom: 2px solid #d5d5d5;
        border-left: 2px solid #d5d5d5;
        border-right: 2px solid #d5d5d5;
        font-size: 20px;
        margin-right: 32px;
        margin-top: -64px;
        padding: 16px;
