<script>
    import Divider from '@/components/template/Divider.vue'
    import { mapState } from 'vuex'

    export default {
        name: 'Equipment',
        components: {
            Divider
        },
        computed: {
            ...mapState({
                initialWeapons: state => state.characters.initialWeapons,
                initialArmors: state => state.characters.initialArmors,
                initialShields: state => state.characters.initialShields,
                maxInitialWeapons: state => state.characters.maxInitialWeapons,
                maxInitialArmor: state => state.characters.maxInitialArmor,
                maxInitialShield: state => state.characters.maxInitialShield,
                equipmentInfo: state => state.equipments.equipmentInfo,
                equipments: state => state.characters.character.equipments,
                equipmentList: state => state.characters.equipmentList,
                geo: state => state.characters.character.geo,
                skills: state => state.characters.character.skills,
            }),
            shields() {
                if (this.skills.length > 0) {
                    for (let skill in this.skills) {
                        if (this.skills[skill].name == 'Combate' && this.skills[skill].specialization.name == 'Escudos') {
                            this.$store.dispatch('characters/getInitialShields')
                            return true
                        }
                    }
                }
                return false
            },
            weapons() {
                if (this.skills.length > 0) {
                    const specializations = []

                    for (let skill in this.skills) {
                       if (this.skills[skill].name == 'Combate' && this.skills[skill].specialization.name != 'Escudos') {
                           specializations.push(this.skills[skill].specialization.id)
                       }
                    }

                    if (specializations.length > 0) {
                        this.$store.dispatch('characters/getInitialWeapons', specializations)
                        return true
                    }
                }
                return false
            },
        },
        methods: {
            chooseArmor(armor) {
                this.$store.dispatch('characters/chooseArmor', armor)
            },
            chooseShield(shield) {
                this.$store.dispatch('characters/chooseShield', shield)
            },
            chooseWeapon(weapon) {
                this.$store.dispatch('characters/chooseWeapon', weapon)
            },
            getEquipmentInfo(id, type) {
                this.$store.dispatch('equipments/getEquipmentInfo', {id, type})
            },
            clearEquipmentInfo() {
                this.$store.dispatch('equipments/clearEquipmentInfo')
            },
            increaseEquipment(equipment) {
                if (confirm('Deseja comprar 1 ' + equipment.name + '?')) {
                    this.$store.dispatch('characters/increaseEquipment',  equipment)
                }
            },
            sellEquipment(equipment) {
                if (confirm('Deseja vender 1 ' + equipment.name + '?')) {
                    this.$store.dispatch('characters/sellEquipment',  equipment)
                }
            }
        },
        created() {
            if (this.initialArmors.length === 0) {
                this.$store.dispatch('characters/getInitialArmors')
            }
        }
    }
</script>

