<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>
        
        <Section name="Aprenda novos Sortilégios" />

        <h3 class="max-spells">Você pode aprender <span>{{ maxSpells }}</span> sortilégio(s).</h3>

        <Select id="affinity" label="Escolha uma Afinidade" :required="true" model="update:affinity" v-model:affinity="affinity">
            <option value=""></option>
            <option value="ether">Ether</option>
            <option value="water">Água</option>
            <option value="air">Brisa</option>
            <option value="fire">Chama</option>
            <option value="earth">Terra</option>
            <option value="light">Luz</option>
            <option value="darkness">Escuridão</option>
            <option value="moon">Lua</option>
            <option value="nature">Natureza</option>
        </Select>

        <div class="spell-row">
            <div v-if="affinity" class="spell-choice">
                <div class="spell-choice-header">
                    <p>Nome</p>
                    <p>Eco</p>
                    <p>Execução</p>
                    <p>Efeito</p>
                    <p>Alcance</p>
                    <p>Duração</p>
                    <p>Resistência</p>
                </div>

                <div class="spell-choice-row" v-for="spell in spellList" :key="spell.id" @click="getSpellInfo(spell.id)">
                    <p>{{ spell.name }}</p>
                    <p>{{ spell.grade }}</p>
                    <p><Activation :value="spell.activation" /></p>
                    <p>{{ spell.effect }}</p>
                    <p>{{ spell.range }}</p>
                    <p>{{ spell.duration }}</p>

                    <p v-if="spell.resistance">{{ spell.resistance }}</p>
                    <p v-else> - </p>
                </div>
            </div>

            <div class="spell-info" v-if="spellSelected">
                <div v-for="spell in spellInfo" :key="spell.id">
                    <h2>
                        {{ spell.name }}
                        <span class="spell-grade">
                            (Eco {{ spell.grade }})
                        </span>
                    </h2>              

                    <p>{{ spell.description }}</p>

                    <p class="spell-activation"><strong>Execução: </strong><Activation :value="spell.activation" /></p>
                    <p><strong>Efeito: </strong>{{ spell.effect }}</p>
                    <p><strong>Alcance: </strong>{{ spell.range }}</p>
                    <p><strong>Duração: </strong>{{ spell.duration }}</p>
                    <p v-if="spell.resistance"><strong>Resistência: </strong>{{ c }}</p>

                    <p class="spell-enchants">Encantos</p>

                    <div v-html="spell.enchants"></div>

                    <button @click="learnSpell(spell)">Aprender {{ spell.name }}</button>
                </div>
            </div>

            <div class="spell-info" v-else>
                <h2>Sortilégios</h2>

                <p>
                    Um sortilégio é composto por uma sequência de palavras que manifestam encantos de acordo com a combinação feita e a energia gasta.
                </p>

                <h3>Características dos Sortilégios</h3>

                <ul>
                    <li><strong>Nome.</strong> O nome dado aquele sortilégio.</li>
                    <li><strong>Execução.</strong> Esta é a quantidade de ações necessária para conjurar o sortilégio. Para sortilégios com execução de ação livre, apenas um pode ser conjurado por rodada. Isso inclui os sortilégios afetados por habilidades que modifiquem sua execução.</li>
                    <li><strong>Alcance.</strong> Define a distância máxima entre o personagem e o local onde o efeito conjurado acontece.</li>
                    <li><strong>Efeito.</strong> Define o tipo de efeito que o sortilégio causa, alguns atingem múltiplos alvos, outros afetam uma área ou mesmo convocam criaturas.</li>
                    <li><strong>Duração.</strong> Indica por quanto tempo o efeito do sortilégio continua ativo.</li>
                    <li><strong>Resistência.</strong> A descrição de cada sortilégio sempre informa o que acontece se o alvo passar ou falhar no teste de resistência, caso só seja informado o tipo de teste assuma sempre que o efeito é evitado completamente em caso de sucesso.</li>
                </ul>
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    import Activation from '@/components/characters/Activation.vue'
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    
    import Section from '@/components/template/Section.vue'
    
    import Select from '@/components/forms/Select.vue'
    
    import { mapState } from 'vuex'

    export default {
        name: 'Equipments',
        components: {
            Activation, ChoiceScreen, Section, Select
        },
        data() {
            return {
                affinity: ''
            }
        },
        computed: {
            ...mapState({
                spellList: state => state.spells.spellList,
                spellSelected: state => state.spells.spellSelected,
                spellInfo: state => state.spells.spellInfo,
                maxSpells: state => state.characters.maxSpells
            })
        },
        watch: {
            affinity() {
                this.$store.dispatch('spells/getSpells', this.affinity)
            }
        },
        methods: {
            back () {
                this.$router.push("/create-character")
                this.$store.dispatch('spells/getSpellState', false)
                document.body.style.overflow = "visible";
                
            },
            getSpellInfo(id) {
                this.$store.dispatch('spells/getSpellInfo', id)
                this.$store.dispatch('spells/getSpellState', true)
            },
            learnSpell(spell) {
                this.$store.dispatch('characters/learnSpell', spell)
                this.$store.dispatch('spells/getSpellState', false)
                this.$router.push("/create-character")
            } 
                
        }
    }
</script>

