import { createRouter, createWebHistory } from 'vue-router'

import store from '@/store/'

import Home from '@/views/Home.vue'
import Finance from '@/views/Finance.vue'
import Game from '@/views/Game.vue'
import Downloads from '@/views/Downloads.vue'
import Profile from '@/views/Profile.vue'
import Characters from '@/views/characters/Characters.vue'
import ShowCharacter from '@/views/characters/ShowCharacter.vue'
import CreateCharacter from '@/views/characters/CreateCharacter.vue'
import HeritagesScreen from '@/views/characters/screens/HeritagesScreen.vue'
import SourcesScreen from '@/views/characters/screens/SourcesScreen.vue'
import MotivationsScreen from '@/views/characters/screens/MotivationsScreen.vue'
import SkillsScreen from '@/views/characters/screens/SkillsScreen.vue'
import EquipmentsScreen from '@/views/characters/screens/EquipmentsScreen.vue'
import SpellsScreen from '@/views/characters/screens/SpellsScreen.vue'
import Login from '@/views/Login.vue'

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/finance',
        name: 'Finance',
        component: Finance
    },
    {
        path: '/game',
        name: 'Game',
        component: Game
    },
    {
        path: '/downloads',
        name: 'Downloads',
        component: Downloads
    },
    {
        path: '/profile',
        name: 'Profile',
        component: Profile,
    },
    {
        path: '/characters',
        name: 'Characters',
        component: Characters,
        children: [
            {
                path: ':id',
                name: 'ShowCharacter',
                component: ShowCharacter,
            }
        ]
    },
    {
        path: '/create-character',
        name: 'CreateCharacter',
        component: CreateCharacter,
        children: [
            {
                path: 'heritages',
                name: 'Heritages',
                component: HeritagesScreen,
            },
            {
                path: 'sources',
                name: 'Sources',
                component: SourcesScreen,
            },
            {
                path: 'motivations',
                name: 'Motivations',
                component: MotivationsScreen,
            },
            {
                path: 'skills',
                name: 'Skills',
                component: SkillsScreen,
            },
            {
                path: 'equipments',
                name: 'Equipments',
                component: EquipmentsScreen,
            },
            {
                path: 'spells',
                name: 'Spells',
                component: SpellsScreen,
            }
        ]
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/:pathMatch(.*)',
        redirect: '/'
    }
]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes,
    scrollBehavior() {
        return { 
            top: 0,
            behavior: 'smooth', 
        }
    }
})

router.beforeEach((to, from, next) => {
    if (to.name === 'Login') {
        if (store.state.login.authenticate) {
            next('/home')
        } else {
            next()
        }
    } else if (to.name === 'Profile' || to.name === 'Characters' || to.name === 'ShowCharacter') { 
        if (store.state.login.authenticate) {
            next()
        } else {
            next('/home')
        }
    } else if (to.name === 'Spells') {
        if (store.state.characters.sorcery) {
            next()
        } else {
            next('/create-character')
        }
    } else {
        next()
    }
  })

export default router