<template>
    <div class="login">
        <div class="login-form">
            <Section name="Login" class="login-form-title" />
            
            <Input id="username" type="text" label="Nome de Usuário" :value="username" model="update:username" v-model:username="username" class="login-input" />
            <Input id="password" type="password" label="Senha" :value="password" model="update:password" v-model:password="password" class="login-input" />

            <button @click="login(username, password)" class="login-button">Logar</button>

            <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in">
                <Alert type="error" v-if="errors.length" class="login-error">
                    <p v-for="(error, index) in errors" :key="index">{{ error }}</p>
                </Alert>
            </transition>

            <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in">
                <Alert type="error" v-if="unauthorized" class="login-error">
                    <p>Usuário ou senha inválidos.</p>
                </Alert>
            </transition>
        </div>
    </div>
</template>

<script>
    import Section from '@/components/template/Section.vue'
    import Input from '@/components/forms/Input.vue'
    import Alert from '@/components/template/Alert.vue'

    export default {
        name: 'Login',
        components: {
            Section, Input, Alert
        },
        data() {
            return {
                username: '',
                password: '',
                errors: []
            }
        },
        computed: {
            unauthorized() {
                return this.$store.state.login.unauthorized
            }
        },
        methods: {
            login(username, password) {
                this.checkErrors()

                if (this.errors.length) {
                    setTimeout(() => {
                        this.errors = []
                    }, 4000)
                } else {
                    this.$store.dispatch('login/authenticate', { username, password })
                    this.clearForm()
                }  
            },
            checkErrors() {
                this.errors = []

                if (!this.username) {
                    this.errors.push(`O campo "Nome de Usuário" é obrigatório.`)
                }

                if (!this.password) {
                    this.errors.push(`O campo "Senha" é obrigatório.`)
                }
            },
            clearForm() {
                this.username = '',
                this.password = ''
            }
        }
    }
</script>

<style scoped>
    .login {
        display: flex;
        flex: 1;
        align-items: center;
        justify-content: center;
    }
    .login-form {
        display: flex;
        flex-direction: column;
        width: 1024px;
    }
    .login-form-title {
        text-align: center;
    }
    .login-input {
        margin: 0 auto 32px;
    }
    .login-button {
        margin: 0 auto;
        width: 120px;
    }
    .login-error {
        margin: 32px auto 0;
        width: 480px;
    }
</style>