<template>
    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" appear>
        <div class="notification" :class="notification.type" v-if="notification.state">
            <p>{{ notification.message }}</p>
        </div>
    </transition>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        name: 'Notification',
        computed: {
            ...mapState({
                notification: state => state.characters.notification,
                notificationState: state => state.characters.notification.state
            })
        },
        watch: {
            notificationState() {
                setTimeout(() => {
                    this.$store.dispatch('characters/clearNotification')
                }, 4000)
            }
        }
    }
</script>

<style scoped>
    .notification {
        background-color: #fff;
        padding: 32px;
        position: fixed;
        right: 0;
        top: 0;
        width: 40%;
        margin: 32px 32px 0 0;

        -webkit-box-shadow: 1px 1px 8px 4px #d5d5d5;
        box-shadow: 1px 1px 8px 4px #d5d5d5;
    }

    .notification.success {
        color: #0f5132;
        background-color: #d1e7dd;

        -webkit-box-shadow: 1px 1px 8px 4px #badbcc;
        box-shadow: 1px 1px 8px 4px #badbcc;
    }
    .notification.error {
        color: #842029;
        background-color: #f8d7da;

        -webkit-box-shadow: 1px 1px 8px 4px #f5c2c7;
        box-shadow: 1px 1px 8px 4px #f5c2c7;
    }
</style>