<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>    

        <Section name="Perícias" />

        <div class="skill-row">
            <div class="skill-choice">
                <div class="skill-choice-header">
                    <p>Nome</p>
                    <p>Afinidade</p>
                    <p class="text-align-center">Requer Especialização</p>
                    <p class="text-align-center">Penalidade de Armadura</p>
                </div>

                <div class="skill-choice-row" v-for="skill in skillList" :key="skill.id" @click="getSkillInfo(skill.id)">
                    <p>{{ skill.name }}</p>

                    <p>{{ skill.affinity_label }}</p>

                    <p class="material-icons text-align-center" v-if="skill.specialization.length > 0">
                        check_box
                    </p>

                    <p class="material-icons text-align-center" v-else> 
                        check_box_outline_blank
                    </p>
    
                    <p class="material-icons text-align-center" v-if="skill.armor_penality">
                        check_box
                    </p>

                    <p class="material-icons text-align-center" v-else>
                        check_box_outline_blank
                    </p>
                </div>
            </div>

            <div class="sources-info" v-show="skillSelected == true">
                <div v-for="skill in skillInfo" :key="skill.id">
                    <h2>{{ skill.name }}</h2>

                    <p>{{ skill.description }}</p>

                    <p><strong>Afinidade.</strong> {{ skill.affinity_label }}</p>
                    
                    <p v-if="skill.armor_penality"><strong>Penalidade de Armadura.</strong> Sim</p>
                    <p v-else><strong>Penalidade de Armadura.</strong> Não</p>
                    
                    <div v-if="skill.specialization.length > 0">
                        <h3>Especializações</h3>

                        <Select id="specialization" label="Escolha uma Especialização" :required="true" model="update:specializationSelected" v-model:specializationSelected="specializationSelected">
                            <option></option>
                            <option v-for="specialization in availableSpecializations()" :key="specialization.id" :value="specialization.id">
                                {{ specialization.name }}
                            </option>
                        </Select>
                    </div>                
                    
                    <h3>Ações</h3>

                    <div class="action-card" v-for="action in skill.actions" :key="action.id">
                        <div class="action-card-header">
                            <div>{{ action.name }}
                                <span v-if="action.trained">
                                    <strong>(Somente Treinado)</strong>
                                </span>
                            </div>

                            <Activation :value="action.activation" />
                        </div>
                        
                        <div class="action-card-info">
                            <p>{{ action.description }}</p>
                            <p><strong>Sucesso.</strong> {{ action.success }}</p>
                            <p><strong>Falha.</strong> {{ action.failure }}</p>
                            <p><strong>Falha Abissal.</strong> {{ action.abissal_failure }}</p>
                        </div>
                    </div>

                    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in">
                        <Alert type="error" v-if="errors.length">
                            <p v-for="(error, index) in errors" :key="index">{{ error }}</p>
                        </Alert>
                    </transition>

                    <button @click="SkillSave(skill, specializationSelected)">Treinar {{ skill.name }}</button>    
                </div>
            </div>

            <div class="sources-info" v-show="skillSelected == false">
                <h2>Perícias</h2>

                <p>Todo aventureiro sabe fazer diversas coisas. Combatentes aprendem a empunhar armas e conduzir batalhas. Especialistas cobrem as mais diversas áreas de artes, ofícios e sociais. Acadêmicos estudam sobre o mundo e seus segredos ocultos.</p>
                <p>Resonance of Ether classifica essas habilidades como perícias. Uma perícia representa algo que um personagem conhece ou sabe fazer, devido à experiência ou estudo.</p>                 

                <h3>Características das Perícias</h3>
                
                <ul>
                    <li><strong>Afinidade.</strong> Toda perícia é baseada em uma afinidade.</li>
                    <li><strong>Penalidade de Armadura.</strong> Algumas perícias exigem liberdade de movimento. Quando o campo Penalidade de Armadura aparece marcado, indica que você sofrerá uma penalidade nos testes dela se estiver usando armadura ou escudo. Vejo a página X para mais detalhes.</li>
                    <li><strong>Ações.</strong> Os tipos de ações que aquela perícia permite realizar.</li>
                </ul>
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    import Activation from '@/components/characters/Activation.vue'
    import Select from '@/components/forms/Select.vue'
    import Section from '@/components/template/Section.vue'
    import Alert from '@/components/template/Alert.vue'
    import { mapState } from 'vuex'

    export default {
        name: 'Skills',
        components: {
            ChoiceScreen, Activation, Select, Section, Alert
        },
        data() {
            return {
                specializationSelected: '',
                errors: []
            }
        },
        computed: {
            ...mapState({
                skillList: state => state.skills.skillList,
                skillSelected: state => state.skills.skillSelected,
                skillInfo: state => state.skills.skillInfo,
                trainedSpecializations: state => state.characters.specializations
            }),
        },
        methods: {
            back() {
                this.$router.push("/create-character")
                this.$store.dispatch('skills/getSkillState', false)
                document.body.style.overflow = "visible";
            },
            availableSpecializations() {
                let SpecializationList = []
                
                if (this.skillInfo.length > 0) {
                    let specializationList = []

                    const specializationInfo = this.skillInfo[0].specialization

                    for (let specialization in specializationInfo) {
                        specializationList.push(specializationInfo[specialization])
                    }

                    const trainedSpecializations = this.trainedSpecializations

                    const newSpecializationList = specializationList.filter(specialization => {
                        return !trainedSpecializations.some(trainedSpecialization => specialization.id === trainedSpecialization.id)
                    })

                    return newSpecializationList
                }

                return SpecializationList
            },
            getSkillInfo(id) {
                this.$store.dispatch('skills/setSkillInfo', id)
                this.$store.dispatch('skills/getSkillState', true)
            },
            checkErrors() {
                this.errors = []

                if(!this.specializationSelected) {
                    this.errors.push(`Escolha uma especialidade.`)
                }
            },
            SkillSave(skill, specializationSelected) {
                if (skill.specialization.length > 0) {
                    this.checkErrors()

                    if (this.errors.length) {
                        setTimeout(() => {
                            this.errors = []
                        }, 4000)
                    } else {
                        this.$store.dispatch('characters/saveSkill', { skill, specializationSelected } )
                        this.$store.dispatch('characters/spentSkillPoints')
                        this.$store.dispatch('skills/updateSkills', { skill, specializationSelected } )
                        this.$router.push("/create-character")   
                    }
                } else {
                    this.$store.dispatch('characters/saveSkill', { skill, specializationSelected } )
                    this.$store.dispatch('characters/spentSkillPoints')
                    this.$store.dispatch('skills/updateSkills', { skill, specializationSelected } )
                    this.$router.push("/create-character")   
                }                
            },
        },
        created() {
            if (this.skillList.length === 0) {
                this.$store.dispatch('skills/getSkills')
            }
        }
    }
</script>

