import api from "@/plugins/axios";

const state = () => ({
    pathList: [],
    magicPathList: [],
    abilityInfo: [],
})

const getters = {}

const actions = {
    async getPaths ({ commit }) {
        const paths = await api.get('/paths/?source=3')
        commit('setPaths', paths.data.results)
    },
    async getMagicPaths ({ commit }) {
        const magicPaths = await api.get('/paths/?source=4')
        commit('setMagicPaths', magicPaths.data.results)
    },
    async getAbilityInfo ({ commit }, id) {
        const ability = await api.get('/abilitys/?id=' +id)
        commit('setAbilityInfo', ability.data.results)        
    },
    clearAbilityInfo ({ commit }) {
        commit('setClearAbilityInfo')
    }
}

const mutations = {
    setPaths (state, paths) {
        state.pathList = paths
    },
    setMagicPaths (state, magicPaths) {
        state.magicPathList = magicPaths
    },
    setAbilityInfo (state, ability) {
        if (state.abilityInfo.length > 0) {
            if (state.abilityInfo[0].id === ability[0].id) {
                state.abilityInfo = []
            } else {
                state.abilityInfo = ability
            }
        } else {
            state.abilityInfo = ability
        } 
    },
    setClearAbilityInfo (state) {
        state.abilityInfo = []
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}