            <p class="path-info" v-if="maxHeritageAbilities > 0">Escolha <span>{{ maxHeritageAbilities }}</span> habilidades:</p>

            <div class="ability-grid">
                <div v-for="ability in heritageAbilities" :key="ability.id">
                    <div class="ability-item" v-if="ability.initial">
                        <p @click="heritageInitialAbility()">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p @click="showAbilityInfo(ability.id)">
                            <span class="material-icons info">info</span>
                        </p>
                    </div>

                    <div class="ability-item" v-else-if="maxHeritageAbilities > 0 || ability.trained == true">
                        <p @click="learnAbility(ability)">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p @click="showAbilityInfo(ability.id)">
                            <span class="material-icons info">info</span>
                        </p>
                    </div>

                    <div class="ability-item" v-else>
                        <p class="disable">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p class="disable">
                            <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                        </p>
                    </div>
                </div>
            </div> 
        </div>

        <div class="paths" v-if="source.length != 0">
            <h3>{{ source.path.name }}</h3>
            <p>{{ source.path.description }}</p>
            <p class="path-info" v-if="maxSourceAbilities > 0">Escolha <span>{{ maxSourceAbilities }}</span> habilidade:</p>

            <div class="ability-grid">
                <div v-for="ability in sourceAbilities" :key="ability.id">
                    <div class="ability-item" v-if="maxSourceAbilities > 0 || ability.trained == true">
                        <p @click="learnAbility(ability)">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p @click="showAbilityInfo(ability.id)">
                            <span class="material-icons info">info</span>
                        </p>
                    </div>

                    <div class="ability-item" v-else>
                        <p class="disable">
                            <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                            <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                            {{ ability.name }}
                        </p>

                        <p class="disable">
                            <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                        </p>
                    </div>
                </div>
            </div> 
        </div>

        <div class="paths">
            <h3>Escolha um caminho de habilidades gerais</h3>

            <Select id="path" label="Lista de caminhos gerais" model="update:pathId" v-model:pathId="pathId" @change="selectPath">
                <option></option>
                <option v-for="path in pathList" :key="path.id" :value="path.id">{{ path.name }}</option>
            </Select>

            <div class="paths" v-if="pathSelected.length > 0">
                <div v-for="path in pathSelected" :key="path.id">
                    <p><strong>{{ path.name }}</strong></p>
                    <p>{{ path.description }}</p>
                    <p class="path-info" v-if="maxPathAbilities > 0">Escolha <span>{{ maxPathAbilities }}</span> habilidade:</p>

                    <div class="ability-grid">
                        <div v-for="ability in path.abilities" :key="ability.id">
                            <div class="ability-item" v-if="maxPathAbilities > 0 || ability.trained == true">
                                <p @click="learnAbility(ability)">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p @click="showAbilityInfo(ability.id)">
                                    <span class="material-icons info">info</span>
                                </p>
                            </div>

                            <div class="ability-item" v-else>
