                                </p>
                            </div>
                        </div>
                    </div> 
                </div>
            </div>

            <h3>Escolha um caminho de habilidades mágicas</h3>

            <Select id="magicPath" label="Lista de caminhos mágicos" model="update:magicPathId" v-model:magicPathId="magicPathId" @change="selectMagicPath">
                <option></option>
                <option v-for="path in magicPathList" :key="path.id" :value="path.id">{{ path.name }}</option>
            </Select>
            
            <div class="paths" v-if="magicPathSelected.length > 0">
                <div v-for="path in magicPathSelected" :key="path.id">
                    <p><strong>{{ path.name }}</strong></p>
                    <p>{{ path.description }}</p>
                    <p class="path-info" v-if="maxPathMagicAbilities > 0">Escolha <span>{{ maxPathMagicAbilities }}</span> habilidade:</p>

                    <div class="ability-grid">
                        <div v-for="ability in path.abilities" :key="ability.id">
                            <div class="ability-item" v-if="maxPathMagicAbilities > 0 || ability.trained == true">
                                <p @click="learnAbility(ability)">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p @click="showAbilityInfo(ability.id)">
                                    <span class="material-icons info">info</span>
                                </p>
                            </div>

                            <div class="ability-item" v-else>
                                <p class="disable">
                                    <span class="material-icons check-box" v-if="ability.trained">check_box</span>
                                    <span class="material-icons check-box" v-else>check_box_outline_blank</span>
                                    {{ ability.name }}
                                </p>

                                <p class="disable">
                                    <span class="material-icons info" @click="showAbilityInfo(ability.id)">info</span>
                                </p>
                            </div>
                        </div>
                    </div> 
                </div>
            </div> 
        </div>

        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" appear>
            <div class="ability-info" v-if="abilityInfo.length > 0">
                <p class="ability-info-close" @click="clearAbilityInfo">
                        <span class="material-icons">cancel</span>
                </p>

                <div v-for="ability in abilityInfo" :key="ability.id">
                    <h3>{{ ability.name }}</h3>

                    <div class="ability-info-header">
                        <p v-if="ability.cost == 99">
                            <strong>Custo.</strong> Afinidade Ativada.
                        </p>

                        <p v-else-if="ability.cost > 0">
                            <strong>Custo.</strong> {{ ability.cost }} {{ ability.resource }}
                        </p>

                        <p v-else>
                            <strong>Custo.</strong> Nenhum.
                        </p>

                        <Activation :value="ability.activation" />
                    </div>

                    <p>{{ ability.description }}</p>
                </div> 
            </div>  
        </transition>
    </div>
</template>

<script>
    import Activation from '@/components/characters/Activation.vue'
    import Divider from '@/components/template/Divider.vue'
    import Select from '@/components/forms/Select.vue'
    import { mapState } from 'vuex'

    export default {
        name: 'Abilities',
        components: {
            Activation, Divider, Select
        },
        data() {
            return {
                pathId: 0,
                magicPathId: 0     
            }
        },
        computed: {
            ...mapState({
                heritage: state => state.characters.character.heritage,
                heritageAbilities: state => state.characters.character.heritageAbilities,
                maxHeritageAbilities: state => state.characters.maxHeritageAbilities,
                source: state => state.characters.character.source,
                sourceAbilities: state => state.characters.character.sourceAbilities,
                maxSourceAbilities: state => state.characters.maxSourceAbilities,
                abilityInfo: state => state.abilities.abilityInfo,
                pathList: state => state.abilities.pathList,
                pathSelected: state => state.characters.pathSelected,
                maxPathAbilities: state => state.characters.maxPathAbilities,
                magicPathList: state => state.abilities.magicPathList,
                magicPathSelected: state => state.characters.magicPathSelected,
                maxPathMagicAbilities: state => state.characters.maxPathMagicAbilities
            })
        },
        methods: {
            heritageInitialAbility() {
                alert('Essa habilidade não pode ser destreinada, ela foi herdada pela sua herança.')
            }, 
            learnAbility(ability) {
                this.$store.dispatch('characters/learnAbility', ability)
            },
            selectPath() {
                if(this.pathId > 0) {
                    this.$store.dispatch('characters/selectPath', this.pathId)
                } else {
                    this.$store.dispatch('characters/clearSelectedPath')
                }
            },
            selectMagicPath() {
                if(this.magicPathId > 0) {
                    this.$store.dispatch('characters/selectPath', this.magicPathId)
                } else {
                    this.$store.dispatch('characters/clearSelectedMagicPath')
                }
                
            },
            showAbilityInfo(id) {
                this.$store.dispatch('abilities/getAbilityInfo', id)            
            },
            clearAbilityInfo() {
                this.$store.dispatch('abilities/clearAbilityInfo')
            },
        },
        created() {
            if (this.pathList.length === 0) {
                this.$store.dispatch('abilities/getPaths')
            }

            if (this.magicPathList.length === 0) {
                this.$store.dispatch('abilities/getMagicPaths')
            }            
        }
    } 
</script>

