<template>
    <div v-if="skills.length > 0">
        <div class="skill-element-row">
            <div class="skill-element-header">
                <p>Perícia</p>
                <p>Afinidade</p>
                <p>Especialização</p>
                <p>Bônus Total</p>
                <p>Treinamento</p>
                <p>Informações</p>
            </div>

            <div v-for="skill in orderedSkillList()" :key="skill.id" class="skill-element-item">
                <p v-if="skill.source_skill">
                    <strong>{{ skill.name }}</strong>
                </p>
                <p v-else>{{ skill.name }}</p>

                <p>{{ skill.affinity_label }}</p>

                <p v-if="skill.specialization.length != 0">{{ skill.specialization.name }}</p>
                <p v-else> - </p>

                <p>+ {{ getAttributeLevel(skill.attribute) + getAffinityLevel(skill.affinity) + skill.training_level }}</p>

                <p class="skill-element-item-training">
                    <span class="material-icons minus" @click="decreaseTraining(skill.id)">remove_circle_outline</span>
                    
                    {{ getSkillLevel(skill.id) }}
                    
                    <span v-if="spentSkillPoints < skillPoints" class="material-icons plus" @click="increaseTraining(skill.id, skill.attribute, skill.affinity)">add_circle_outline</span>
                    <span v-else class="material-icons plus disable">add_circle_outline</span> 

                    [ Máximo + {{ getMaxTraining(skill.attribute, skill.affinity) }} ]
                </p>

                <p class="skill-element-item-details">
                    <span class="material-icons info" @click="getSkillDetails(skill.id)">info</span>
                </p>
            </div>
        </div>

        <p class="source-skill-info">* Perícias em negrito foram recebidas pela sua origem.</p>
    </div>

    <div v-else class="not-skills" >
        <p>Você ainda não treinou nenhuma perícia.</p>
    </div>

    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in" appear>
        <div class="skill-detail" v-if="skillDetail != 0">
            <p class="skill-detail-close " @click="clearSkillDetail">
                <span class="material-icons">cancel</span>
            </p>

            <h2 v-if="skillDetail.specialization.length === 0">{{ skillDetail.name }}</h2>
            <h2 v-else>{{ skillDetail.name }} ( {{ skillDetail.specialization.name }} )</h2>

            <p>{{ skillDetail.description }}</p>

            <hr>

            <p>
                <strong>Modificador Total</strong>
                <span class="skill-detail-pill-bg">+{{ getAttributeLevel(skillDetail.attribute) + getAffinityLevel(skillDetail.affinity) + skillDetail.training_level - armorPenality }}</span> = 
                <span class="skill-detail-pill">{{ skillDetail.attribute_label }} + {{ getAttributeLevel(skillDetail.attribute) }}</span> +
                <span class="skill-detail-pill">{{ skillDetail.affinity_label }} + {{ getAffinityLevel(skillDetail.affinity) }}</span> +
                <span class="skill-detail-pill">Treinamento + {{ skillDetail.training_level }}</span>
                <span class="skill-detail-pill" v-if="skillDetail.armor_penality"> - Penalidade de Armadura - {{ armorPenality }}</span>
            </p>

            <hr>

            <div class="skill-detail-wrapper">
                <div class="skill-detail-actions" @click="changeSkillActionState()">
                    <h3 class="skill-detail-actions-tittle">Ações
                        <span v-if="skillActionState" class="material-icons">arrow_drop_up</span>
                        <span v-else class="material-icons">arrow_drop_down</span>
                    </h3>
                    <div v-show="skillActionState === true">
                        <div class="skill-detail-action-card" v-for="action in skillDetail.actions" :key="action.id">
                            <div class="skill-detail-action-card-header">
                                <div>{{ action.name }}
                                    <span v-if="action.trained">
                                        <strong>(Somente Treinado)</strong>
                                    </span>
                                </div>

                                <Activation :value="action.activation" />
                            </div>
                            
                            <div class="skill-detail-action-card-info">
                                <p>{{ action.description }}</p>
                                <p><strong>Sucesso.</strong> {{ action.success }}</p>
                                <p><strong>Falha.</strong> {{ action.failure }}</p>
                                <p><strong>Falha Abissal.</strong> {{ action.abissal_failure }}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import { mapState } from 'vuex'
    import Activation from '@/components/characters/Activation.vue'

    export default {
        name: 'SkillElement',
        components: {
            Activation
        },
        data() {
            return {
                skillActionState: false
            }
        },
        computed: {
            ...mapState({
                mind: state => state.characters.character.mind,
                cunning: state => state.characters.character.cunning,
                skills: state => state.characters.character.skills,
                skillDetail: state => state.characters.skillDetail,
                armorPenality: state => state.characters.armorPenality,
                spentSkillPoints: state => state.characters.spentSkillPoints,
                skillPoints: state => state.characters.skillPoints
            }),
            skillPoints() {
                return (this.mind *5) + this.cunning
            }
        },
        methods: {
            orderedSkillList() {
                return this.skills.sort(function (a, b) {
                    if (a.name > b.name) {
                        return 1;
                    }
                    if (a.name < b.name) {
                        return -1;
                    }

                    return 0;
                });
            },
            getAttributeLevel(attribute) {
                return this.$store.state.characters.character[attribute]
            },
            getAffinityLevel(affinity) {
                return this.$store.state.characters.character[affinity]
            },
            getSkillLevel(id) {
                const skill = this.skills.find(skill => skill.id === id)

                if(skill) return skill.training_level

                return 0
            },
            getMaxTraining(attribute, affinity) {
                let attributeLevel = this.$store.state.characters.character[attribute]
                let affinityLevel = this.$store.state.characters.character[affinity]

                return attributeLevel + affinityLevel
            },
            getSkillDetails(id) {
                this.$store.dispatch('characters/getSkillDetail', id)
            },
            clearSkillDetail() {
                this.$store.dispatch('characters/clearSkillDetail')
            },
            changeSkillActionState() {
                this.skillActionState = !this.skillActionState
            },
            increaseTraining(id, attribute, affinity) {
                const currentSkill = this.skills.find(skill => skill.id === id)
                const maxSkillLevel = this.getMaxTraining(attribute, affinity)

                if (maxSkillLevel === currentSkill.training_level) {
                    alert('Você atingiu o nível máximo de treinamento permitido nessa perícia.')
                } else {
                    currentSkill.training_level = currentSkill.training_level +1
                    this.$store.dispatch('characters/spentSkillPoints')
                }
            },
            decreaseTraining(id) {
                const currentSkill = this.skills.find(skill => skill.id === id)
                
                if (currentSkill.training_level === 1) {
                    if (currentSkill.source_skill === true) {
                        alert('Você não pode destreinar uma perícia recebida pela sua origem')
                    } else {
                        if (confirm('Deseja destreinar essa perícia?')) {
                            this.$store.dispatch('characters/addSkillPoints')
                            this.$store.state.characters.skillDetail = []

                            const getIndex = this.skills.indexOf(currentSkill.id)
                            this.skills.splice(getIndex, 1)
                        }
                    } 
                } else {
                    currentSkill.training_level = currentSkill.training_level -1
                    this.$store.dispatch('characters/addSkillPoints')
                }                
            }
        }
    }
</script>

