import api from "@/plugins/axios";
import store from '@/store/'

const state = () => ({
    spellList: [],
    spellSelected: false,
    spellInfo: [],
    enchants: [],
    enchantInfo: []
})

const getters = {}

const actions = {
    async getSpells ({ commit }, affinity) {
        const getAffinity = await api.get('/afinitys/?name=' +affinity)
        const spells = await api.get('/spells/?afinity=' +getAffinity.data.results[0].id)
        commit('setSpells', spells.data.results)
    },
    async getSpellInfo({ commit }, id) {
        const spell = await api.get('/spells/?id=' +id)
        commit('setSpellInfo', spell.data.results)     
    },
    async getEnchants({ commit }) {
        const enchants = await api.get('/enchants/')
        commit('setEnchants', enchants.data.results)     
    },
    async getEnchantInfo({ commit }, id) {
        const enchant = await api.get('/enchants/?id=' +id)
        commit('setEnchantInfo', enchant.data.results)     
    },
    chooseEnchant({ commit }, enchant) {
        commit('setChooseEnchant', enchant)
    },
    getSpellState ({ commit }, spellState) {
        commit('setSpellState', spellState)
    },
    deleteEnchant({ commit }) {
        commit('setDeleteEnchant')
    }
}

const mutations = {
    setSpells(state, spells) {
        let spellList = []

        const characterSpells = store.state.characters.character.spells

        if (characterSpells.length > 0) {
            for (let spell in spells) {
                const findSpell = characterSpells.find(el => el.id === spells[spell].id)

                if (!findSpell) {
                    spellList.push(spells[spell])
                }
            }
        } else {
            spellList = spells
        }

        state.spellList = spellList
    },
    setSpellInfo (state, spell) {
        state.spellInfo = spell
    },
    setEnchants (state, enchants) {
        state.enchants = enchants
    },
    setEnchantInfo (state, enchant) {
        state.enchantInfo = enchant
    },
    setChooseEnchant (state, enchant) {
        store.state.characters.character.enchants.push(enchant)
        store.state.characters.maxEnchants = store.state.characters.maxEnchants -1
    },
    setSpellState (state, spellState) {
        state.spellSelected = spellState
    },
    setDeleteEnchant () {
        store.state.characters.character.enchants = []
        store.state.characters.maxEnchants = store.state.characters.maxEnchants +1
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}