import api from "@/plugins/axios";

const state = () => ({
    equipmentList: [],
    equipmentSelected: false,
    equipmentInfo: [],
    screenEquipmentInfo: []
})

const getters = {}

const actions = {
    async getEquipment ({ commit }, equipmentCategory) {
        const equipment = await api.get('/' + equipmentCategory + '/')
        commit('setEquipment', equipment.data.results)
    },
    async getEquipmentInfo({ commit }, {id, type}) {
        const equipment = await api.get('/' + type + '/?id=' +id)
        commit('setEquipmentInfo', equipment.data.results)     
    },
    async getScreenEquipmentInfo({ commit }, {id, equipmentCategory}) {
        const equipmentInfo = await api.get('/' + equipmentCategory + '/?id=' +id)
        commit('setScreenEquipmentInfo', equipmentInfo.data.results)
    },
    getEquipmentState ({ commit }, equipmentState) {
        commit('setEquipmentSelected', equipmentState)
    },
    clearEquipmentInfo ({ commit }) {
        commit('setClearEquipmentInfo')
    }
}

const mutations = {
    setEquipment(state, equipment) {
        state.equipmentList = equipment
    },
    setEquipmentInfo (state, equipment) {
        if (state.equipmentInfo.length > 0) {
            if (state.equipmentInfo[0].id === equipment[0].id) {
                state.equipmentInfo = []
            } else {
                state.equipmentInfo = equipment
            }
        } else {
            state.equipmentInfo = equipment
        } 
    },
    setScreenEquipmentInfo (state, equipmentInfo) {
        state.screenEquipmentInfo = equipmentInfo
    },
    setEquipmentSelected (state, equipmentState) {
        state.equipmentSelected = equipmentState
    },
    setClearEquipmentInfo (state) {
        state.equipmentInfo = []
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}