import api from "@/plugins/axios";

const state = () => ({
    heritageInfo: [],
    heritageSelected: false,
    heritagesList: [],  
})

const getters = {}

const actions = {
    async getHeritages ({ commit }) {
        const heritages = await api.get('/heritages/?fields=id,name')
        commit('setHeritages', heritages.data.results)
    },
    async getHeritageInfo ({ commit }, id) {
        const heritageInfo = await api.get('/heritages/?id=' + id)
        commit('setHeritageInfo', heritageInfo.data.results)
    },
    getHeritageState ({ commit }, heritageState) {
        commit('setHeritageSelected', heritageState)
    }
}

const mutations = {
    setHeritages (state, heritages) {
        state.heritagesList = heritages
    },
    setHeritageInfo (state, heritageInfo) {
        state.heritageInfo = heritageInfo
    },
    setHeritageSelected (state, heritageState) {
        state.heritageSelected = heritageState
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}