<template>
    <div class="component-form-row">
        <label class="component-label" :for="id">{{ label }}</label>
        <select 
            class="component-select" 
            :id="id"
            :required="required"
            @input="$emit(model, $event.target.value)"
        >
            <slot>
                {{ option }}    
            </slot>
            
        </select>
    </div>
</template>

<script>
    export default {
        name: 'Select',
        props: {
            id: {
                type: String
            },
            label: {
                type: String,
                required: true
            },
            required: {
                type: Boolean,
                default: false
            },
            option: {
                type: String
            },
            model: {
                type: String,
                default: '',
                required: true
            }
        }
    }
</script>

