<template>
    <ChoiceScreen>
        <a @click="back()">
            <span class="material-icons">arrow_back</span>
            Voltar
        </a>
        
        <Section name="Origens" />

        <div class="sources-row">
            <div class="sources-choice">
                <div class="sources-card" v-for="source in sourceList" :key="source.id" @click="getSourceInfo(source.id)">
                    <h2>{{ source.name }}</h2>
                </div>
            </div>
            
            <div class="sources-info" v-show="sourceSelected == true">
                <div v-for="source in sourceInfo" :key="source.id">
                    <h2>{{ source.name }}</h2>

                    <p>{{ source.description }}</p>

                    <hr>

                    <h3>Perícias</h3>
                    
                    <div v-if="source.primary_skill !== null && source.secondary_skill !== null">
                        <p v-if="source.primary_skill.specialization.length === 0">{{ source.primary_skill.name }} +1</p>
                        <p v-else>
                            <span>{{ source.primary_skill.name }} +1</span>
                            <Select 
                                class="sources-info-form" 
                                id="primarySpecialization" 
                                label="Especialização" 
                                :required="true" 
                                model="update:primarySpecialization" 
                                v-model:primarySpecialization="primarySpecialization"
                            >
                                <option></option>
                                <option v-for="specialization in source.primary_skill.specialization" :key="specialization.id" :value="specialization.id">{{ specialization.name }}</option>
                            </Select>
                        </p>

                        <p v-if="source.secondary_skill.specialization.length === 0">{{ source.secondary_skill.name }} +1</p>
                        <p v-else>
                            <span>{{ source.secondary_skill.name }} +1</span>
                            <Select 
                                class="sources-info-form" 
                                id="secondarySpecialization" 
                                label="Especialização" 
                                :required="true" 
                                model="update:secondarySpecialization" 
                                v-model:secondarySpecialization="secondarySpecialization"
                            >
                                <option></option>
                                <option v-for="specialization in source.secondary_skill.specialization" :key="specialization.id" :value="specialization.id">{{ specialization.name }}</option>
                            </Select>
                        </p>
                    </div>

                    <p v-else>O contador de histórias define 2 perícias para receber (+1) de treinamento.</p>

                    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut" mode="out-in">
                        <Alert type="error" v-if="errors.length">
                            <p v-for="(error, index) in errors" :key="index">{{ error }}</p>
                        </Alert>
                    </transition>

                    <hr>

                    <p><strong>Ímpeto.</strong> {{ source.impulse.description }}</p>

                   <button @click="sourceSave(source, primarySpecialization, secondarySpecialization)">Escolher {{ source.name }}</button>
                </div>
            </div>

            <div class="sources-info" v-show="sourceSelected == false">   
                <h2>Origens</h2>

                <p>Sua herança diz como você nasceu e sua origem diz como você viveu. Ela representa o que você fez até se tornar quem é hoje.</p>
                <p>No 1º nível, quando cria seu personagem, você escolhe uma origem. Estas escolha é permanente, você não pode mudá-la depois.</p>                 

                <h3>Características das Origens</h3>
                
                <ul>
                    <li><strong>Perícias.</strong> Treinamento (+1) em duas perícias.</li>
                    <li><strong>Impeto.</strong> Um ímpeto relacionado com aquela origem.</li>
                </ul>
            </div>
        </div>
    </ChoiceScreen>
</template>

<script>
    import ChoiceScreen from '@/components/characters/ChoiceScreen.vue'
    import Section from '@/components/template/Section.vue'
    import Select from '@/components/forms/Select.vue'
    import Alert from '@/components/template/Alert.vue'
    import { mapState } from 'vuex'

    export default {
        name: 'Sources',
        components: {
            ChoiceScreen, Section, Select, Alert
        },
        data() {
            return {
                primarySpecialization: '',
                secondarySpecialization: '',
                errors: []
            }
        },
        computed: {
            ...mapState({
                sourceInfo: state => state.sources.sourceInfo,
                sourceSelected: state => state.sources.sourceSelected,
                sourceList: state => state.sources.sourceList,
            })
        },
        methods: {
            back () {
                this.$router.push("/create-character")
                this.$store.dispatch('sources/getSourceState', false)
                document.body.style.overflow = "visible";
                
            },
            getSourceInfo(id) {
                this.$store.dispatch('sources/getSourceInfo', id)
                this.$store.dispatch('sources/getSourceState', true)
            },
            checkErrors() {
                this.errors = []

                if(!this.primarySpecialization || !this.secondarySpecialization) {
                    this.errors.push(`Escolha as duas especializações das perícias de origem.`)
                }
            },
            sourceSave(source, primarySpecialization, secondarySpecialization) {
                this.checkErrors()

                if (this.errors.length) {
                    setTimeout(() => {
                        this.errors = []
                    }, 4000)
                } else {
                    this.$store.dispatch('characters/getCharacterSource', { source, primarySpecialization, secondarySpecialization })
                    this.$router.push("/create-character")
                }
            }
        },
        created() {
            if (this.sourceList.length === 0) {
                this.$store.dispatch('sources/getSources')
            }
        }
    }
</script>

